import React, { Component } from 'react';
import '../../App.css';
import { Layout,Input,Button,Table,Form,Dialog } from 'element-react';

class RoleManage extends Component{

    handleClickForEdit(e,row){
        this.setState({
            dialogVisible: true,
            dialogData: row
        })
    }

    constructor(props) {
        super(props);

        this.state = {
            columns: [
                {
                    label: "",
                    prop: "roleName",
                },
                {
                    label: "",
                    prop: "zip",
                    width: '160%',
                    render: (row) => {
                        return <span>
                                    <Button type="text" size="small" onClick={e => this.handleClickForEdit(e,row)}></Button>
                                    <Button type="text" size="small"></Button><Button type="text" size="small"></Button>
                                </span>
                    }
                }
            ],
            data: [{
                roleName: ''
            },{
                roleName: ''
            },{
                roleName: ''
            },{
                roleName: ''
            }],
            dialogVisible: false,
            dialogData:''
        }
    }

    render(){
        const { dialogVisible,dialogData,columns,data } = this.state
        return (
            <div>
                <h3></h3>
                <Layout.Col span={8}>
                    <div className="UserGroupManage-table">
                        <Table
                            columns={columns}
                            data={data}
                            border={true}
                        />
                    </div>
                </Layout.Col>
                <h4></h4>
                <Layout.Col span={10}>
                    <p><Input placeholder="" className="inline-input"/></p>
                    <Button type="primary" size="small"></Button>
                    <Button type="primary" size="small"></Button>
                </Layout.Col>
                <div>
                    <Dialog
                        title=""
                        visible={ dialogVisible }
                        onCancel={ e => this.setState({ dialogVisible: false }) }
                        dialogData={ dialogData }
                        size="tiny"
                    >
                        <Dialog.Body>
                            <Form>
                                <Form.Item label="" labelWidth="80">
                                    <Input placeholder={dialogData.roleName} className="inline-input"></Input>
                                </Form.Item>
                            </Form>
                        </Dialog.Body>
                        <Dialog.Footer className="dialog-footer">
                            <Button type="primary" onClick={ () => this.setState({ dialogVisible: false }) }> </Button>
                        </Dialog.Footer>
                    </Dialog>
                </div>
            </div>
        );
    }
}

export default RoleManage;


