import React, { Component } from 'react';
import '../../App.css';
import './ApartmentManage.css';
import { Layout,Tree,Input,Button } from 'element-react';

class ApartmentManage extends Component{

    componentWillMount(){
        console.log(1111)
        this.$get('/dept/list',{})
            .then(res=>{
                console.log(res)
            })
    }

    handleClick(data){
        this.setState({
            placeholder: data.label
        })
    }

    constructor(props) {
        super(props);

        this.handleClick = this.handleClick.bind(this);

        this.state = {
            data: [{
                id: 1,
                label: '',
                children: [{
                    id: 2,
                    label: '',
                    children:[{
                        id: 4,
                        label: ""
                    },{
                        id: 5,
                      label: ""
                    }]
                },{
                    id: 3,
                    label: "",
                    children:[{
                        id: 6,
                        label: ""
                    },{
                        id: 7,
                        label: ""
                    }]
                }]
            }],
            options: {
                children: 'children',
                label: 'label'
            },
            placeholder : ''
        };
    }


    render(){
        const { data,options,placeholder } = this.state;
        return(
            <div>
                <h3></h3>
                <Layout.Col span={4}>
                    <span></span>
                    <div className="ApartmentManage-tree">
                        <Tree
                            data={data}
                            options={options}
                            nodeKey="id"
                            defaultExpandedKeys={[1,2,3]}
                            onNodeClicked={this.handleClick.bind(data.label)}
                        />
                    </div>
                </Layout.Col>
                <Layout.Col span={13}>
                    <div className="ApartmentManage-context-1">
                        <span></span>
                        <Input placeholder={ placeholder } className="inline-input"/>
                        <Button type="primary" size="small"></Button>
                    </div>
                    <div className="ApartmentManage-context-2">
                        <span></span>
                        <Input placeholder={ placeholder } className="inline-input"/>
                        <span></span>
                        <Input placeholder="" className="inline-input"/>
                        <Button type="primary" size="small"></Button>
                    </div>
                    <div className="ApartmentManage-context-2">
                        <div><Input placeholder={ placeholder } className="inline-input"/></div>
                        <div>
                            <Input placeholder="" className="inline-input"/>
                            <span></span>
                        </div>
                        <Button type="primary" size="small"></Button>
                    </div>
                </Layout.Col>
            </div>
        );
    }
}

export default ApartmentManage;


