import React, { Component } from 'react';
import '../../App.css';
import './Menu.css';
import { Layout,Menu } from 'element-react';
import { Link } from 'react-router';
import PureRenderMixin from 'react-addons-pure-render-mixin';

class Menus extends Component{

    constructor(props) {
        super(props);
        this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this);
    }

    render(){
        return(
                <Layout.Col span={5}>
                    <Menu className="el-menu-vertical-demo" defaultOpeneds={['1']} uniqueOpened = "true" >
                        <Menu.SubMenu className={this.props.className} index="1" title="">
                            <Menu.SubMenu index='2' title={<span></span>}>
                                <Menu.SubMenu index='3' title={<p></p>}>
                                    <Link to='/contents/ApartmentManage'><Menu.Item index="3-1"></Menu.Item></Link>
                                    <Link to='/contents/UserManage'><Menu.Item index="3-2"></Menu.Item></Link>
                                    <Link to='/contents/UserGroupManage'><Menu.Item index="3-3"></Menu.Item></Link>
                                    <Link to='/contents/RoleManage'><Menu.Item index="3-4"></Menu.Item></Link>
                                    <Link to='/contents/ModulesManage'><Menu.Item index="3-5"></Menu.Item></Link>
                                </Menu.SubMenu>
                            </Menu.SubMenu>
                            <Menu.SubMenu index="4" title={<span></span>}>
                              <Menu.SubMenu index='3' title={<p></p>}>
                                  <Link to='/dataManage/economicIndicatorsSetting/economicIndicators'><Menu.Item index="3-1"></Menu.Item></Link>
                                  <Link to='/contents/economicIndicatorsSetting/UserManage'><Menu.Item index="3-2"></Menu.Item></Link>
                                  <Link to='/contents/economicIndicatorsSetting/UserGroupManage'><Menu.Item index="3-3"></Menu.Item></Link>
                                  <Link to='/contents/economicIndicatorsSetting/RoleManage'><Menu.Item index="3-4"></Menu.Item></Link>
                              </Menu.SubMenu>
                              </Menu.SubMenu>

                            <Menu.SubMenu index="5" title={<span></span>}>
                                <Menu.Item index="1-1">3</Menu.Item>
                            </Menu.SubMenu>
                            <Menu.SubMenu index="6" title={<span></span>}>
                                <Menu.Item index="1-1">3</Menu.Item>
                            </Menu.SubMenu>
                            <Menu.SubMenu index="7" title={<span></span>}>
                                <Menu.Item index="1-1">3</Menu.Item>
                            </Menu.SubMenu>
                            <Menu.SubMenu index="8" title={<span></span>}>
                                <Menu.Item index="1-1">3</Menu.Item>
                            </Menu.SubMenu>
                            <Menu.SubMenu index="9" title={<span></span>}>
                                <Menu.Item index="1-1">3</Menu.Item>
                            </Menu.SubMenu>
                        </Menu.SubMenu>
                    </Menu>
                </Layout.Col>
        );
    }
}


export default Menus;


