function get (url, params = {}){
  return new Promise((reslove,reject) => {
    Axios.get(url,{
      params:params
    }).then(
      (response) =>{
        reslove(response.data);
      },
      (err) => {

        reject(err);
      }
    )
  })
}

 function post(url, data = {}){
  return new Promise((reslove,reject) => {
    Axios.post(url,data)
      .then(
        (response) => {
          reslove(response);
        }).catch(
      (error) => {
        reject(error);
      })
  })
}
